
.aside-nav{position:fixed;top:5.5em;display:inline-block;width:11.5em
}
.aside-nav .nav-list{padding:0;margin:0
}
.aside-nav .nav-list .item{border:none;display:block;width:100%;height:3em;line-height:3em;padding:0 1em;text-decoration:none;text-transform:uppercase;font-weight:700;border-radius:1px;color:#777;font-family:CenturyGothic;margin-bottom:.5em
}
.aside-nav .nav-list .item:last-child{margin-bottom:0
}
.aside-nav .nav-list .item:hover{color:#2196f3
}
.aside-nav .nav-list .item.router-link-active{background-color:#eee;font-weight:bold;color:#2196f3;background-color:rgba(255,255,255,0.6)
}
.aside-nav .nav-list .item .iconfont{width:1em;margin-right:1em;display:inline-block
}



