
.article>.detail,.article>.metas,.article>.related{margin-bottom:1em;background-color:rgba(255,255,255,0.6)
}
.article>.detail{padding:1em 2em
}
.article>.detail>.title{text-align:center;margin:1em 0 1.5em 0;font-weight:700
}
.article>.detail>.content p{line-height:2.2em;text-indent:2em;margin-bottom:1em
}
.article>.detail>.content h1,.article>.detail>.content h2,.article>.detail>.content h3,.article>.detail>.content h4,.article>.detail>.content h5,.article>.detail>.content h6{margin:1em 0;padding-left:0;line-height:1.8em;font-weight:700;text-indent:0
}
.article>.detail>.content blockquote p{text-indent:0em
}
.article>.detail>.content code{display:block;padding:2em 2em 1.5em 2em;background-color:rgba(0,0,0,0.8);position:relative;overflow:visible
}
.article>.detail>.content code:before{color:white;content:attr(lang);height:2.5em;line-height:2.5em;font-size:1.1em;position:absolute;top:0;left:0;width:100%;font-weight:700;background-color:#444;display:block;text-transform:uppercase;text-align:center
}
.article>.metas{padding:1em 2em
}
.article>.related{padding:.8em;overflow:hidden
}
.article>.related>.article-lists{padding:0;margin:0;list-style:none;overflow:hidden;opacity:.9
}
.article>.related>.article-lists>.item{float:left;margin-right:.8em;margin-bottom:.8em
}
.article>.related>.article-lists>.item:nth-child(4),.article>.related>.article-lists>.item:nth-child(8){margin-right:0
}
.article>.related>.article-lists>.item:nth-child(1n+5){margin-bottom:0
}
.article>.related>.article-lists>.item>.item-box{display:block;position:relative;width:9.61em;height:9em;overflow:hidden
}
.article>.related>.article-lists>.item>.item-box:hover .thumb{opacity:.9;-khtml-transform:scale(1.2) rotate(3deg);transform:scale(1.2) rotate(3deg);transition:all 1s
}
.article>.related>.article-lists>.item>.item-box>.thumb{width:100%;height:100%
}
.article>.related>.article-lists>.item>.item-box>.title{position:absolute;bottom:0;left:0;width:100%;height:2em;line-height:2em;background-color:rgba(165,165,165,0.7);padding:0 .5em;color:white;opacity:.8
}



