
.projects{min-height:40em
}
.projects>.project-list{padding:0;margin:0
}
.projects>.project-list>.item{display:inline-block;float:left;margin-right:1em;margin-bottom:1em
}
.projects>.project-list>.item:nth-child(4),.projects>.project-list>.item:nth-child(8),.projects>.project-list>.item:nth-child(12),.projects>.project-list>.item:nth-child(16){margin-right:0
}
.projects>.project-list>.item:nth-child(1n+9){margin-bottom:0
}
.projects>.project-list>.item>.item-content{display:block;width:14.8em;height:18em;padding:1em;text-align:center;background-color:rgba(255,255,255,0.6)
}
.projects>.project-list>.item>.item-content:hover{background-color:rgba(197,197,197,0.4)
}
.projects>.project-list>.item>.item-content>.iconfont{display:block;height:1.3em;font-size:5em
}
.projects>.project-list>.item>.item-content>.title{margin-bottom:1em;text-transform:capitalize
}
.projects>.project-list>.item>.item-content>.desc{margin-bottom:0;text-align:left;line-height:2em;text-indent:1em
}



