
aside{float:right;display:block;width:19em;margin:0
}
aside .aside-search,aside .aside-article,aside .aside-calendar,aside .aside-ad,aside .aside-tag{background-color:rgba(255,255,255,0.6)
}
aside .aside-search{margin-bottom:1em
}
aside .aside-search>.search-box{padding:.5em;overflow:hidden
}
aside .aside-search>.search-box>.search-input,aside .aside-search>.search-box>.search-btn,aside .aside-search>.search-box>.rss-btn{background-color:rgba(197,197,197,0.4);height:2em;line-height:2em;float:left
}
aside .aside-search>.search-box>.search-input:hover,aside .aside-search>.search-box>.search-btn:hover,aside .aside-search>.search-box>.rss-btn:hover{background-color:rgba(146,146,146,0.4)
}
aside .aside-search>.search-box>.search-input{margin-right:0
}
aside .aside-search>.search-box>.search-btn{width:2em;background-color:rgba(146,146,146,0.4)
}
aside .aside-search>.search-box>.search-btn:hover{background-color:rgba(95,95,95,0.4)
}
aside .aside-search>.search-box>.rss-btn{float:right;width:4em
}
aside>.aside-article{overflow:hidden;margin-bottom:1em
}
aside>.aside-article>.title{height:3em;line-height:3em;margin:0;padding:0 .8em;border-bottom:1px dashed #eee
}
aside>.aside-article>.title .iconfont{margin-right:.5em
}
aside>.aside-article>.aside-article-list{list-style:none;padding:.5em 0;margin-bottom:0;counter-reset:hot-article-list
}
aside>.aside-article>.aside-article-list .item{display:block;height:1.9em;line-height:1.9em;padding:0 .8em;margin-bottom:.5em;color:#333;overflow:hidden;text-overflow:ellipsis;white-space:nowrap
}
aside>.aside-article>.aside-article-list .item:last-child{margin:0
}
aside>.aside-article>.aside-article-list .item .index{counter-increment:hot-article-list;background-color:rgba(197,197,197,0.4);width:1.5em;height:1.5em;line-height:1.5em;display:inline-block;text-align:center;margin-right:.5em;color:#777;font-size:.8em
}
aside>.aside-article>.aside-article-list .item .index::before{content:counter(hot-article-list)
}
aside>.aside-article>.aside-article-list .item .title:hover{margin-left:.5em;text-decoration:underline
}
aside .aside-calendar{padding:.8em;margin-bottom:1em
}
aside .aside-ad{padding:.8em;margin-bottom:1em
}
aside .aside-ad>.ad-box{opacity:.8
}
aside .aside-ad>.ad-box img{max-width:100%;border-top:1px solid #1c4767
}
aside .aside-tag{width:19em;padding:.8em;padding-right:0;padding-bottom:0;margin-bottom:1em
}
aside .aside-tag.fixed{position:fixed;top:5.5em
}
aside .aside-tag .aside-tag-list{list-style:none;padding:0;margin:0;overflow:hidden
}
aside .aside-tag .aside-tag-list .item{display:inline-block;float:left;margin-right:.8em;margin-bottom:.8em;height:2em;line-height:2em;text-transform:capitalize;background-color:rgba(197,197,197,0.4)
}
aside .aside-tag .aside-tag-list .item:hover{background-color:rgba(95,95,95,0.4)
}
aside .aside-tag .aside-tag-list .item .title{display:block;padding:0 .5em;font-family:CenturyGothic, -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", "Hiragino Sans GB", "Segoe UI", "Microsoft YaHei", sans-serif
}



