
#tools{position:fixed;width:100%;bottom:20em
}
#tools>.container{position:relative
}
#tools>.container>.tools-list{position:absolute;right:-10em;width:3em;height:6em
}
#tools>.container>.tools-list>.to-top,#tools>.container>.tools-list>.feedback{display:block;width:3em;height:3em;text-align:center;background-color:rgba(255,255,255,0.6)
}
#tools>.container>.tools-list>.to-top:hover,#tools>.container>.tools-list>.feedback:hover{background-color:rgba(197,197,197,0.4)
}



